import React, { useState } from 'react';
import {BrowserRouter as Router, Route} from 'react-router-dom'
import Swal from 'sweetalert2';
import withReactComponent from 'sweetalert2-react-content';
import './App.css';
import Nav from '../components/Nav.jsx';
import Cards from '../components/Cards.jsx';
import About from '../components/About.jsx';
import City from '../components/City.jsx'
import Footer from '../components/Footer';

const apiKey = '4ae2636d8dfbdc3044bede63951a019b';

const MySwal = withReactComponent(Swal);

function App() {

  const [cities, setCities] = useState([]);

  function onClose(id) {
    setCities(oldCities => oldCities.filter(c => c.id !== id));
  }

  function addCity(city) {

    setCities(oldCities => [...oldCities, city]);

    /* <---- POP UP Message ----> */
    MySwal.fire({
      position: 'top-end',
      icon: 'success',
      titleText: `${city.name} was added succesfully...`,
      showConfirmButton: false,
      timer: 1500
    })
  }

  function onSearch(city) {
    //Llamado a la API del clima
    fetch(`https://api.openweathermap.org/data/2.5/weather?q=${city}&appid=${apiKey}&units=imperial`)
      .then(r => r.json())
      .then((recurso) => {
        if(recurso.main !== undefined){
          
          const city = {
            min: Math.round(recurso.main.temp_min),
            max: Math.round(recurso.main.temp_max),
            img: recurso.weather[0].icon,
            id: recurso.id,
            wind: recurso.wind.speed,
            temp: recurso.main.temp,
            name: recurso.name,
            weather: recurso.weather[0].main,
            clouds: recurso.clouds.all,
            latitud: recurso.coord.lat,
            longitud: recurso.coord.lon
          };

          cities && cities.filter(city => city.id === recurso.id).length ?
          MySwal.fire({
            text: `${city.name} was already added on your list.`,
            padding: '0.75rem',
            showConfirmButton: false,
            timer: '2500'
          }) :
            addCity(city);
        } else {
          MySwal.fire({
            icon: 'error',
            text: `The city was not found.`,
            padding: '0.75rem',
            showConfirmButton: false,
            timer: '2500'
          })
        }
      });
  }

  function onFilter(ciudadId) {
    let ciudad = cities.filter(c => c.id === parseInt(ciudadId));
    if(ciudad.length > 0) {
        return ciudad[0];
    } else {
        return null;
    }
  }

  return (
    <Router>
      <div className='flex flex-col h-screen' id='wholeContainer'>
        <Route
        path='/weather-app'
        render={() => <Nav onSearch={onSearch}/>}
        />
        <div className='flex-grow'>
          <Route
          exact path='/weather-app'
          render={() => <Cards
          cities={cities}
          onClose={onClose}/>}
          />
          <Route
          exact path='/weather-app/About'
          component={About}/>
          <Route
          path='/weather-app/city/:cityId'
          render={({match}) => <City city={onFilter(match.params.cityId)}/>}
          />
        </div>
        <Route path='/' component={Footer} />
      </div>
    </Router>
  );
}

export default App;
